.edit-container {
    h3 {
        text-align: center;
    }
    form {
        text-align: center;
        input {
            margin-right: 10px;
            color: $clr1;
            background-color: $clr4;
            padding: 10px;
            border-radius: 5px;
        }
        button {
            padding: 10px;
            border: 0;
            background-color: $clr3;
            cursor: pointer;
            &:hover{
                background-color: $clr2;
            }
        }
    }
}
